export default function ProjectsCard({
    title,
    year,
    description,
    image,
    tecnologies,
    link
}: {
    title: string;
    year: string;
    description: string;
    image: string;
    tecnologies: React.ReactNode[];
    link: string;
}) {
    return (
        <>
            <article className="grid grid-cols-2[1fr_auto] grid-rows-4[auto_auto_1fr_auto] gap-2 w-content border-zinc-800 border rounded-lg p-2 flex flex-col items-start justify-center gap-2">

                <header className="col-span-2 flex flex-row items-center justify-center gap-2">
                    <div className="text-xl font-bold">{title}</div>
                    <div className="text-lg text-zinc-400">{year}</div>
                </header>

                <div className="row-start-2">
                    {image}
                </div>

                <div className="col-start-1 row-start-3">
                    {description}
                </div>

                <div className="row-span-3 col-start-2 row-start-2">
                    {tecnologies}
                </div>

                <div className="row-start-4">
                    {link}
                </div>

            </article>
        </>
    );
}