import ProjectsCard from "@/components/ProjectsCard";
import Briefcase_outline from "@/components/icons/brieftcase_outline";

import SVGJava from "@/components/icons/tecnologies/java";
import SVGAndroidStudio from "@/components/icons/tecnologies/android_studio";
import SVGPHP from "@/components/icons/tecnologies/php";
import SVGMySQL from "@/components/icons/tecnologies/mysql";
import SVGJSON from "@/components/icons/tecnologies/json";
import SVGJavaScript from "@/components/icons/tecnologies/javascript";
import SVGXampp from "@/components/icons/tecnologies/xampp";
import SVGFirebase from "@/components/icons/tecnologies/firebase";
import SVGPython from "@/components/icons/tecnologies/python";
import SVGChatGPT from "@/components/icons/tecnologies/chatgpt";

export default function Projects() {

    const svgJava = <SVGJava size={32} />;
    const svgAndroidStudio = <SVGAndroidStudio size={32} />;
    const svgPhp = <SVGPHP size={32} />;

    return (
        <>
            <section className="w-full flex flex-col items-center justify-center gap-2 rounded-lg p-2 mt-24">

                <article className="w-full flex flex-col items-start justify-center gap-2 rounded-lg p-2" id="projects">
                    <div className="bg-zinc-900 text-2xl font-bold w-content rounded-lg p-2 flex flex-row items-center gap-2">
                        <Briefcase_outline size={36} color="white" />
                        <span>
                            Proyectos
                        </span>
                    </div>
                    <div className="bg-zinc-900 text-base text-gray-200/80 font-bold rounded-lg pl-2 pr-2">
                        <span>
                            Conocimiento y experiencia aplicados en desarrollo de proyectos
                        </span>
                    </div>
                </article>

                <article className="w-full bg-red-500 rounded-lg p-2">
                    <ProjectsCard
                        title="Proyecto Test"
                        year="2022"
                        image="https://via.placeholder.com/150"
                        description="Descripción del proyecto 1"
                        tecnologies={[svgAndroidStudio, svgJava, svgPhp]}
                        link="https://github.com/ernxx"
                    />

                </article>

            </section>
        </>
    );
}